#character {
    flex: 0 0 50%;
    width: 100% !important;
    height: 100vh !important;
    position: relative;
    top: 0;
    z-index: 1;
    div {
        position: absolute;
        right: 50%;
        transform: translateX(50%);
        width: 100% !important;
        height: 100vh !important;
    }
}